.for-who{
  position: relative;
  z-index: 2;
  padding-top: 3em;
  &__inner{
    border-top: $border;
    border-bottom: $border;
    padding-left: 4.6875em;
    position: relative;
  }
  &__row{
    display: grid;
    grid-template-columns: 33.625em calc(100% - 33.625em);
    border-left: $border;
    &._type2{
      border-top: $border;
      .for-who__title{
        span{
          background: linear-gradient(115deg, #ec5fef 0%, #ef5f5f 100%);
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
      .for-who__col2{
        padding: 7em 4em;
        max-width: 24.5em;
        border-right: $border;
      }
      .for-who__link{
        span{
          background: linear-gradient(103deg, #ec5fef 0%, #ef5f5f 100%);
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
    }
  }
  &__col1{
    padding: 3.5em 4.5em;
    border-right: $border;
  }
  &__title{
    font-size: 4em;
    letter-spacing: -.02em;
    font-weight: 700;
    text-transform: uppercase;
    span{
      font-style: italic;
      background: linear-gradient(106deg, #ffc368 0%, #ff8540 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__col2{
    padding: 5em 1.75em 5em 4em;
  }
  &__top{
    display: grid;
    grid-template-columns: 12.75em 14em;
    justify-content: space-between;
    gap: 3em;
  }
  &__desc{
    font-size: .75em;
    line-height: 200%;
    letter-spacing: .04em;
    font-weight: 600;
    color: $gray;
  }
  &__bottom{
    display: flex;
    margin-top: 2.25em;
  }
  &__link{
    display: flex;
    align-items: center;
    color: #FFC368;
    font-weight: 800;
    img{
      width: .4375em;
      margin-left: 1em;
    }
    span{
      font-size: .6875em;
      line-height: 145%;
      letter-spacing: .16em;
    }
  }
  &__bg{
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2;
    transform: translate(-50%,-50%);
    max-width: 100%;
    pointer-events: none;
  }
  &__nav{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    left: 0;
    bottom: 3.5em;
  }
  &__btn{
    width: .75em;
    margin-top: 2em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 576px){
  .for-who{
    padding-top: 2.75em;
    &__inner{
      padding: 0;
    }
    &__row{
      grid-template-columns: 100%;
      border-left: none;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 1px;
        height: 19.5em;
        background-color: rgba($gray,.16);
      }
      &._type2{
        border-right: $border;
        &:before{
          display: none;
        }
        .for-who__title{
          ._small{
            font-size: .83em;
          }
        }
        .for-who__col2{
          padding: 0 3.25em 2.75em 0;
          border: none;
        }
        .for-who__bottom{
          margin-top: 3em;
        }
      }
    }
    &__top{
      grid-template-columns: 100%;
    }
    &__col1{
      padding: 3em 3em 3em 0;
      border-right: none;
    }
    &__col2{
      padding: 0 0 2.75em;
    }
    &__title{
      font-size: 3em;
    }
    &__text{
      max-width: 12.75em;
    }
    &__text2{
      padding-left: 5.5em;
      border-left: 1px solid rgba($gray,.24);
    }
    &__bottom{
      margin-top: 4.625em;
      max-width: 13.75em;
    }
    &__nav{
      display: none;
    }
  }
}
