.footer{
  &__inner{
    padding: 4.5em 0 5.5em;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid rgba($gray,.24);
  }
  &__logo{
    display: block;
    width: 6.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    font-size: .6875em;
    line-height: 290%;
    font-weight: 800;
    color: rgba($gray,.4);
    text-transform: uppercase;
    letter-spacing: .16em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    margin-top: 6.25em;
    &__inner{
      flex-wrap: wrap;
      padding: 2.5em 0 3em;
    }
    &__col{
      width: 100%;
      margin-bottom: 3em;
    }
    &__col2{
      order: 4;
      width: 100%;
      margin-top: 3em;
      display: flex;
      justify-content: flex-end;
    }
    &__logo{
      margin: 0 auto;
    }
  }
}
