.country{
  padding: 2em 0 3em;
  &__top{
    @include grid100gap(1em);
    text-align: center;
    text-transform: uppercase;
  }
  &__title{
    font-size: 1.25em;
    letter-spacing: .32em;
    font-weight: 800;
  }
  &__desc{
    font-size: .625em;
    line-height: 240%;
    color: $gray;
    font-weight: 700;
    letter-spacing: .09em;
  }
  &__content{
    margin-top: -.875em;
    position: relative;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__text{
    position: absolute;
    top: 4.875em;
    left: 50%;
    transform: translateX(-50%);
    @include grid100gap(.5em);
    text-align: center;
    text-transform: uppercase;
    z-index: 2;
    &-line,
    &-line2,
    &-line3,
    &-line4{
      font-weight: 300;
      font-style: italic;
      letter-spacing: -.04em;
    }
    &-line{
      font-size: 8em;
      letter-spacing: -.04em;
      font-weight: 800;
      background: radial-gradient(104.23% 98.06% at 22.34%  5.93%, rgb(156, 98, 255) 0%, rgb(80, 14, 193) 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &-line2{
      font-size: 5.5em;
    }
    &-line3{
      font-size: 7.25em;
    }
    &-line4{
      font-weight: 800;
      font-size: 3em;
      color: #7E8186;
    }
  }
  &__bottom{
    display: flex;
    justify-content: center;
    margin-top: -11.875em;
    position: relative;
    z-index: 3;
  }
}

.country-select{
  position: relative;
  &._open{
    .country-select__hidden{
      opacity: 1;
      pointer-events: unset;
    }
    .country-select__arrow{
      transform: rotate(-180deg);
    }
  }
  &__show{
    backdrop-filter: blur(24px);
    padding: 0 1.5em;
    height: 3.5625em;
    background-color: rgba(#818E9D,.08);
    border: 1.5px solid rgba(#F4F9FF,.16);
    border-radius: 1.25em;
    display: flex;
    align-items: center;
    font-weight: 700;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      border-color: rgba(#F4F9FF,.36);
    }
  }
  &__current{
    display: flex;
    align-items: center;
  }
  &__placeholder{
    font-size: .625em;
    text-transform: uppercase;
    letter-spacing: .09em;
  }
  &__arrow{
    width: .75em;
    margin-left: .5em;
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-top: .8125em;
    width: 13.875em;
    padding: 1.5em;
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    background-color: rgba(#818E9D,.16);
    border-radius: 1.25em;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background-color: rgba(#818E9D,.16);
      border-radius: 1.25em;
      backdrop-filter: blur(24px);
    }
  }
  &__groups{
    overflow-y: auto;
    max-height: 12.75em;
    &::-webkit-scrollbar {
      width: 1px;
      height: 1px;
    }
    &::-webkit-scrollbar-track {
      background: #343D45;
      border-radius: 16px;
    }
    &::-webkit-scrollbar-thumb {
      background: $white;
      border-radius: 16px;
    }
  }
}

.country-group{
  @include lastM(0 0 1.5em);
  &__title{
    font-size: .625em;
    font-weight: 700;
    letter-spacing: .09em;
    color: $gray;
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(1em);
  }
}

.country-item{
  display: flex;
  align-items: center;
  transition: all .3s;
  &:hover{
    cursor: pointer;
    color: $primary;
  }
  &__img{
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    margin-right: .9375em;
  }
  &__title{
    font-size: .75em;
    letter-spacing: .04em;
    font-weight: 600;
  }
}


@media screen and (max-width: 576px){
  .country{
    padding: 3.5em 0 2.75em;
    &__top{
      position: relative;
      z-index: 2;
    }
    &__content{
      margin-top: -6.625em;
    }
    &__img{
      width: calc(100% + 5em);
      margin-left: -2.5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__text{
      position: static;
      transform: unset;
      gap: 1em;
      margin-top: -12em;
      &-line{
        font-size: 6.25em;
      }
      &-line2{
        font-size: 5em;
      }
      &-line3{
        font-size: 6em;
      }
      &-line4{
        font-size: 2.5em;
      }
    }
    &__bottom{
      margin-top: 4em;
    }
  }
}
