.m-ellipse{
  position: absolute;
  width: 55.875em;
  pointer-events: none;
}

.about{
  padding: 3.5em 0 0;
  position: relative;
  .container{
    padding: 0 3em;
  }
  &__inner{
    padding: 5em 3.5em 3em;
    border-radius: 4em;
    background-color: #0C0E11;
    position: relative;
    overflow: hidden;
    z-index: 2;
  }
  &__bg,
  &__bg2,
  &__bg3{
    pointer-events: none;
    position: absolute;
  }
  &__bg{
    top: 0;
    left: 0;
    width: 34.1875em;
    z-index: 2;
  }
  &__bg2{
    top: 0;
    left: 6.375em;
    width: 21.4375em;
  }
  &__bg3{
    width: 16.5em;
    right: 0;
    bottom: 0;
  }
  &__bg4{
    right: 0;
    top: -13em;
    max-width: 100%;
  }
  &__bg5{
    left: 0;
    top: -13em;
    max-width: 100%;
  }
}

.about-top{
  position: relative;
  z-index: 3;
  display: grid;
  grid-template-columns: 19.1875em 48.375em;
  justify-content: space-between;
  &__col{
    padding-top: 6.25em;
  }
  &__text{
    text-align: center;
    @include grid100gap(1.125em);
    text-transform: uppercase;
  }
  &__title{
    font-size: 1em;
    font-weight: 700;
    letter-spacing: .32em;
  }
  &__desc{
    font-size: .6875em;
    font-weight: 800;
    letter-spacing: .16em;
    color: $gray;
  }
  &__contacts{
    margin-top: 5.625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-title,
    &-link{
      font-size: .75em;
      letter-spacing: .04em;
      font-weight: 600;
    }
    &-title{
      color: $gray;
    }
  }
  &__logo{
    width: 100%;
    display: block;
  }
}

.about-center{
  display: grid;
  grid-template-columns: 24.25em calc(100% - 24.25em);
  border-top: $border;
  border-bottom: $border;
  margin-top: 4em;
  &__col{
    padding: 5em 5em 5em 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-right: $border;
  }
  &__text,
  &__link{
    font-size: .75em;
    letter-spacing: .04em;
    line-height: 200%;
  }
  &__text{
    font-weight: 600;
    color: $gray;
  }
  &__link{
    margin-top: 3em;
    font-weight: 700;
    @media screen and (min-width: 577px){
      background: radial-gradient(517.60% 100.84% at -0.82%  93.06%, rgb(136, 66, 255) 0%, rgb(86, 166, 224) 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__col2{
    padding: 5em 0 5em 5em;
  }
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__title,
  &__desc{
    letter-spacing: -.02em;
    font-weight: 700;
    font-style: italic;
    text-transform: uppercase;
  }
  &__title{
    font-size: 2em;
  }
  &__desc{
    font-size: 1em;
    margin-left: 1em;
    color: $gray;
    margin-bottom: .125em;
  }
}

.partners{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 3.75em;
  &__item{
    height: 3.25em;
    img{
      height: 100%;
      display: block;
    }
  }
}

.subscribe{
  position: relative;
  z-index: 3;
  margin-top: 4.5em;
  border-radius: 1.5em 1.5em 4em 4em;
  background-image: url(../img/subscribe-bg.png);
  background-size: cover;
  background-position: center;
  padding: 3.125em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title{
    font-size: 2em;
    text-align: center;
    font-style: italic;
    letter-spacing: -.02em;
    font-weight: 700;
  }
}

.subscribe-form{
  position: relative;
  width: 18.375em;
  margin-top: 1em;
  max-width: 100%;
  &__icon,
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  &__icon{
    left: 0;
    width: 1.125em;
  }
  &__btn{
    right: 0;
    width: 2em;
    height: 2em;
    background-color: #EFF6FF;
    @include center;
    border-radius: 50%;
    svg{
      width: .4375em;
    }
    &:hover{
      background-color: $primary;
      path{
        stroke: #fff;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    padding: 0;
    .container{
      padding: 0 1em;
    }
    &__inner{
      padding: 3em 2em 2.5em;
    }
    &__bg2,
    &__bg3,
    &__bg4,
    &__bg5{
      display: none;
    }
  }

  .about-top{
    grid-template-columns: 100%;
    gap: 3em;
    &__col{
      padding: 0 0 3em;
      border-bottom: $border;
    }
    &__contacts{
      margin-top: 2.375em;
    }
  }

  .about-center{
    margin-top: 3em;
    display: block;
    padding: 3em 0 0;
    border-bottom: none;
    &__col{
      padding: 0 0 3em;
      border-right: none;
      border-bottom: $border;
    }
    &__link{
      max-width: 80%;
      span{
        background: radial-gradient(517.60% 100.84% at -0.82%  93.06%, rgb(136, 66, 255) 0%, rgb(86, 166, 224) 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
    &__col2{
      padding: 3em 0 0;
    }
  }

  .partners{
    margin-top: 1em;
    flex-wrap: wrap;
    justify-content: unset;
    width: calc(100% + 2.5em);
    &__item{
      margin: 2em 2.5em 0 0;
      height: 1.9375em;
    }
  }

  .subscribe{
    background-image: url(../img/subscribe-bg2.png);
    margin-top: 3em;
    border-radius: 2em;
    padding: 2.5em 1.5em;
    &__title{
      font-size: 1.5em;
    }
  }
}
