.main{
  background-image: url(../img/main-lines.svg), url(../img/main-bg.jpg);
  background-size: 100% auto, cover;
  background-position: 0 0, center;
  min-height: 64.25em;
  padding: 15.375em 0 11em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 14.5em;
    background: linear-gradient(180.00deg, rgba(5, 5, 6, 0),rgb(5, 5, 6) 100%);
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 41em) 39.875em;
    justify-content: space-between;
  }
  &__left{
    padding-bottom: 4em;
    display: flex;
    flex-direction: column;
  }
  &__right{
    align-self: flex-start;
    padding-top: 20.875em;
    position: relative;
  }
  &__scroll{
    margin-top: 4em;
    @include center;
    .text11{
      letter-spacing: .16em;
    }
    img{
      margin-left: 1.5em;
      width: .625em;
    }
  }
  &__btn{
    position: absolute;
    width: 7.3125em;
    height: 7.3125em;
    @include center;
    border: 1.5px dashed #b285ff;
    border-radius: 50%;
    right: 20.5625em;
    bottom: 6.25em;
    svg{
      width: 1.125em;
    }
  }
}

.stores{
  display: flex;
  &__item{
    font-weight: 700;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 3.5em;
    &:not(:hover){
      color: #9DAAB9;
      img{
        opacity: .5;
      }
    }
    &:hover{
      color: #fff;
    }
    img{
      transition: all .3s;
      height: 2.5em;
      margin-bottom: 1.5em;
    }
    span{
      letter-spacing: 0.16em;
    }
  }
}

.main-item,
.main-item2,
.main-item3{
  &__img{
    @include imgCover(133%);
  }
}

.main-item{
  display: grid;
  grid-template-columns: 12.8125em calc(100% - 12.8125em - 2.75em);
  justify-content: space-between;
  max-width: 28.6875em;
  &__img{
    border-radius: 4em 0 1.5em 0;
    box-shadow: -6px 8px 22px 0px rgba(24, 28, 38, 0.14),-24px 32px 40px 0px rgba(24, 28, 38, 0.12),-53px 72px 54px 0px rgba(24, 28, 38, 0.07),-94px 128px 64px 0px rgba(24, 28, 38, 0.02),-147px 200px 69px 0px rgba(24, 28, 38, 0);
  }
  &__text{
    padding-top: 1.875em;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    .text10{
      line-height: 240%;
      letter-spacing: .09em;
    }
  }
  &__star{
    display: block;
    margin: 1em auto 0;
    width: 2.5em;
  }
}

.main-text{
  margin-top: auto;
  padding-top: 7.25em;
  &__title{
    padding: 2.25em 0 0 1em;
  }
  .text14{
    letter-spacing: .32em;
  }
}

.main-item2{
  position: absolute;
  width: 8.75em;
  right: 8.25em;
  top: 3em;
  &__img{
    border-radius: 0 1em 0 3em;
  }
}

.main-item3{
  position: absolute;
  width: 7.75em;
  right: -6.5em;
  bottom: 5.625em;
  &__img{
    box-shadow: -3px 4px 10px 0px rgba(35, 44, 52, 0.12),-13px 14px 19px 0px rgba(35, 44, 52, 0.1),-28px 32px 26px 0px rgba(35, 44, 52, 0.06),-50px 57px 30px 0px rgba(35, 44, 52, 0.02),-78px 89px 33px 0px rgba(35, 44, 52, 0);
    border-radius: 1.5em 0 0 0;
  }
}

.main-num{
  position: absolute;
  width: 7.5em;
  top: 3em;
  left: 11.375em;
  img{
    display: block;
    width: 100%;
  }
}



@media screen and (max-width: 576px){
  .main{
    padding: 0;
    &__wrap{
      padding: 8.125em 0 5em;
      overflow: hidden;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 4em;
    }
    &__left{
      padding: 0;
      order: 2;
    }
    &__right{
      padding-top: 0;
      position: static;
    }
    &__items{
      position: relative;
    }
    &__scroll{
      margin-top: 3.125em;
    }
    &__btn{
      margin: 7.5em auto 0;
      position: relative;
      z-index: 2;
      right: unset;
      bottom: unset;
    }
  }

  .main-text{
    padding-top: 4em;
    &__title{
      padding-top: 3.5em;
    }
  }

  .main-item{
    grid-template-columns: 100%;
    gap: 3.375em;
    max-width: 13.125em;
    &__img{
      width: 10.5625em;
      height: 14.75em;
      padding: 0;
    }
    &__text{
      text-align: left;
      padding-top: 0;
      display: flex;
      flex-direction: column;
    }
    &__star{
      order: -1;
      margin: 0 0 1.5em;
    }
  }

  .main-item2{
    top: -1.25em;
    right: -5em;
  }

  .main-item3{
    right: -5.625em;
    bottom: -4em;
  }

  .main-num{
    top: 9.5em;
    right: 2em;
    left: unset;
  }
}
