@media screen and (max-width: 576px){
  .gallery{

  }

  .gallery-slider{
    width: calc(100% + 18em);
    margin-left: -9em;
    .swiper{
      &-container{
        width: calc(100% + .375em);
      }
      &-slide{
        height: 13.25em;
        padding-right: .375em;
        &-next{
          .gallery__img{
            height: 10em;
          }
          &+.swiper-slide{
            .gallery__img{
              height: 10em;
            }
            &+.swiper-slide{
              .gallery__img{

              }
            }
          }
        }
      }
      &-nav{
        position: relative;
        z-index: 2;
        bottom: unset;
        transform: unset;
        width: 100%;
        left: unset;
        justify-content: center;
      }
      &-button{
        width: auto;
        height: auto;
        margin: 0 1.5em;
        background: transparent;
        box-shadow: unset;
        border: none;
        backdrop-filter: unset;
        &:before{
          display: none;
        }
        &:after{
          display: block;
          font-family: $font1;
          font-size: .625em;
          letter-spacing: .09em;
          text-transform: uppercase;
          font-weight: 700;
          color: $gray;
        }
        &-next{
          &:after{
            content: 'NEXT';
          }
          svg{
            margin-right: 1.5em;
          }
        }
        &-prev{
          &:after{
            content: 'PREV';
            order: -1;
          }
          svg{
            margin-left: 1.5em;
          }
        }
      }
    }
  }
}
