.benefits{
  padding: 5em 0 3.5em;
  position: relative;
  &__text{
    text-align: center;
  }
  &__subtitle{
    font-weight: 700;
    font-size: .875em;
    letter-spacing: 0.32em;
    text-transform: uppercase;
    color: $primary;
  }
  &__title{
    margin-top: 2.5em;
    text-transform: uppercase;
    .text116{
      letter-spacing: 0.32em;
    }
    span{
      display: inline-block;
      background: radial-gradient(107.50% 222.59% at 477.23%  -7.50%, rgb(156, 98, 255) 0%, rgb(80, 14, 193) 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__desc{
    margin: 3em auto 0;
    width: 20em;
    max-width: 100%;
    color: $gray;
    font-weight: 600;
    .text12{
      line-height: 200%;
      letter-spacing: .04em;
    }
  }
  &__wrap{
    display: flex;
    justify-content: center;
  }
  &__nav{
    position: relative;
    margin-top: 2.5em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: #fff;
    span{
      font-size: 1em;
      letter-spacing: .04em;
    }
    img{
      margin-left: 1.5em;
      width: 3em;
    }
    &:hover{
      color: $primary;
    }
  }
  &__items{
    margin-top: .875em;
    display: flex;
    justify-content: center;
  }
  &__bg{
    position: absolute;
    width: 64em;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    top: 100%;
    margin-top: -18.75em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.benefit-item{
  margin: 0 2.5em;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  @media screen and (min-width: 577px){
    &:nth-child(2),
    &:nth-child(3){
      margin-top: 3.5em;
    }
  }
  &__icon{
    width: 5em;
    height: 5em;
    border-radius: 1em;
    box-shadow: -1px 2px 5px 0px rgba(11, 11, 14, 0.1),-5px 7px 9px 0px rgba(11, 11, 14, 0.09),-12px 16px 12px 0px rgba(11, 11, 14, 0.05),-22px 28px 14px 0px rgba(11, 11, 14, 0.01),-34px 43px 15px 0px rgba(11, 11, 14, 0);
    background: rgb(24, 24, 32);
    @include center;
    margin-bottom: 1.5em;
    img{
      height: 1.5em;
    }
  }
  &__title{
    font-size: .625em;
    line-height: 160%;
    letter-spacing: .09em;
    font-weight: 800;
    text-transform: uppercase;
  }
}

.benefits-modal{
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  max-width: calc(100vw - 2em);
  width: 22.75em;
  padding: 2.5em 2em;
  margin-top: 1.5em;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._open{
    opacity: 1;
    pointer-events: unset;
  }
  &:before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: rgba(#818E9D,.16);
    border-radius: 1.25em;
    backdrop-filter: blur(24px);
  }
  &__top{
    max-width: 15.625em;
    margin: 0 auto;
  }
  &__title{
    font-size: .75em;
    line-height: 200%;
    font-weight: 400;
    text-align: center;
  }
}

.benefits-form{
  margin-top: 1.25em;
  display: flex;
  align-items: flex-end;
  .fg{
    flex-grow: 1;
    input{
      height: 4em;
    }
  }
  &__btn{
    flex-shrink: 0;
    padding: 0 1.5em;
    height: 2em;
    background: #2f80ed;
    color: $white;
    border-radius: 1em;
    margin-left: 1em;
    font-weight: 600;
    &:hover{
      background: darken(#2f80ed,10%);
      color: $white;
    }
    span{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .benefits{
    padding: 1.875em 0 5em;
    &__title{
      margin-top: 2em;
      .text116{
        font-size: 3.5em;
      }
    }
    &__desc{
      margin-top: 2.5em;
      width: 16.25em;
    }
    &__nav{
      margin-top: 3em;
    }
    &__items{
      margin-top: 1.5em;
      flex-wrap: wrap;
      width: calc(100% + 2.25em);
    }
    &__bg{
      overflow: hidden;
      display: flex;
      justify-content: center;
      margin-top: -18em;
      img{
        height: 28.1875em;
        width: auto;
      }
    }
  }

  .benefit-item{
    margin: 2.5em 2em 0 0;
  }
}
