.fg {
  position: relative;
  margin: 0;
  input{
    border-radius: 0;
    border: none;
    border-bottom: 1px solid rgba($white,.16);
    padding: 0 4em 0 2.67em;
    height: 6.66em;
    width: 100%;
    font-size: .75em;
    background-color: transparent;
    color: #000;
    font-weight: 600;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $white;
    }
  }
}


