.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 6em 0 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 6.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.menu{
  display: flex;
  align-items: flex-start;
  .lang{
    margin-left: 11em;
  }
}

.nav{
  display: flex;
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include lastM(0 12.5em 0 0);
  }
  &__link,
  &__item{
    @include lastM(0 0 2em);
    font-weight: 800;
    text-transform: uppercase;
  }
  &__link,
  &__show{
    transition: all .3s;
    position: relative;
    span{
      font-size: .6875em;
      letter-spacing: .16em;
    }
    svg{
      width: .625em;
      margin-left: .5em;
    }
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      right: 100%;
      transform: translateY(-50%);
      width: .25em;
      height: .25em;
      border-radius: 50%;
      background-color: #FF7979;
      margin-right: 1em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &:hover{
      cursor: pointer;
      color: #FF7979;
      &:before{
        opacity: 1;
      }
      path{
        stroke: #FF7979;
      }
    }
  }
  &__show{
    &:before{
      display: none;
    }
  }
  &__link{
    &--disabled{
      color: $gray;
    }
  }
}

.m-dropdown{
  position: relative;
  &:hover{
    .m-dropdown__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-top: -1px;
    z-index: 10;
    min-width: 100%;
    padding-top: .875em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__inner{
    background-color: rgba(#818E9D,.16);
    border-radius: 1.25em;
    position: relative;
    padding: 1.125em 1.25em;
    min-width: 100%;
    white-space: nowrap;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background-color: rgba(#818E9D,.16);
      border-radius: 1.25em;
      backdrop-filter: blur(24px);
    }
  }
  &__link{
    @include lastM(0 0 .875em);
    span{
      font-size: .6875em;
      letter-spacing: .16em;
    }
  }
}

.lang{
  font-weight: 800;
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    svg{
      margin-left: 1em;
      width: .75em;
    }
  }
  .m-dropdown__hidden{
    min-width: 4.625em;
  }
  .m-dropdown__show span,
  .m-dropdown__link span{
    font-size: .875em;
    letter-spacing: .04em;
  }
}


@media screen and (max-width: 576px){
  .header{
    padding-top: 2.75em;
    &__inner{
      align-items: center;
    }
    &__logo{
      position: relative;
      z-index: 11;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    overflow-y: auto;
    position: fixed;
    z-index: 10;
    flex-direction: column;
    width: 100%;
    height: $vh;
    background-color: $black;
    top: 0;
    left: 0;
    padding: 6em 2.5em 2em;
    transition: all .4s;
    opacity: 0;
    pointer-events: none;
    .lang{
      margin: 3em 0 0;
    }
  }

  .nav{
    flex-direction: column;
    &__group{
      @include lastM(0 0 2em);
    }
    .m-dropdown{
      &__show{
        svg{
          display: none;
        }
      }
      &__hidden{
        padding: 1.25em 0 0 2em;
        opacity: 1;
        pointer-events: unset;
        transform: unset;
        position: static;
      }
      &__inner{
        background-color: transparent;
        padding: 0;
        &:before{
          display: none;
        }
      }
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 2em;
    height: 2em;
    margin-right: -.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 1em;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 1.5em;
    }
  }
}
