.news{
  background-image: url(../img/news-bg.svg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 70.375em auto;
  padding: 5.75em 0 4em;
  &__top{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid rgba($gray,.24);
    padding-bottom: 3em;
  }
  &__subtitle{
    font-size: 1em;
    text-transform: uppercase;
    opacity: .6;
    letter-spacing: .32em;
    font-weight: 400;
    margin-bottom: 1em;
  }
  &__title{
    font-size: 4em;
    font-weight: 700;
    font-style: italic;
    letter-spacing: -.02em;
  }
  &__link{
    margin-top: 2em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 26em calc(100% - 52em) 26em;
    margin-top: 4em;
  }
  &__col{
    display: flex;
    flex-direction: column;
    &:nth-child(1){
      .news__img:last-child{
        border-radius: 0 0 0 5em;
      }
    }
    &:nth-child(3){
      .news__img:first-child{
        border-radius: 0 5em 0 0;
      }
      .news__img:last-child{
        border-radius: 0 0 5em 0;
      }
    }
  }
  &__img{
    width: 100%;
    display: block;
  }
}

.m-link{
  font-weight: 800;
  color: $gray;
  .text11{
    letter-spacing: .16em;
  }
  svg{
    margin-left: 1.25em;
    width: .4375em;
  }
}

.news-item{
  padding: 3em;
  background-color: $white;
  color: $black;
  border-radius: 5em 0 0 0;
  flex-grow: 1;
  &__title{
    font-weight: 700;
    font-size: 2.5em;
    line-height: 120%;
    letter-spacing: -.04em;
    i{
      font-style: italic;
    }
  }
  &__bottom{
    margin-top: 3em;
    display: grid;
    grid-template-columns: calc(100% - 6.5em) 2.5em;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__text{
    font-size: .75em;
    line-height: 200%;
    font-weight: 600;
    letter-spacing: .04em;
    color: $gray;
  }
  &__link{
    width: 2.5em;
    height: 2.5em;
    @include center;
    border-radius: 50%;
    border: 1px solid rgba(#1f2123,.16);
    svg{
      width: .4375em;
    }
    &:hover{
      background-color: $primary;
      border-color: $primary;
      path{
        stroke: $white;
      }
    }
  }
}

.news-item2,
.news-item3{
  background-color: $white;
  padding: 2.875em 3em;
  flex-grow: 1;
  color: $black;
  display: flex;
}

.news-item2{
  align-items: flex-end;
  justify-content: space-between;
  &__text{
    font-weight: 700;
  }
  &__title1{
    font-size: 1.75em;
    color: $gray;
    line-height: 143%;
    letter-spacing: -0.04em;
    i{
      font-style: italic;
    }
  }
  &__title2{
    font-size: 2.5em;
    letter-spacing: -0.04em;
  }
}

.news-item3{
  flex-direction: column;
  align-items: flex-start;
  &__title{
    font-size: 2em;
    letter-spacing: -0.04em;
    font-weight: 700;
    font-style: italic;
  }
  .news-item__link{
    margin-top: 1em;
  }
}


@media screen and (max-width: 576px){
  .news{
    padding: 3.75em 0 1.25em;
    background-image: unset;
    &__top{
      padding-bottom: 4em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      &--big{
        width: calc(100% + 2.5em);
      }
      &--small{
        width: calc(100% - 2.5em);
      }
    }
  }

  .news-item{
    padding: 2.5em 2em;
    position: relative;
    &__bottom{
      margin-top: 2em;
      grid-template-columns: 100%;
    }
    .news-item__link{
      position: absolute;
      top: 2.5em;
      right: 2em;
    }
  }

  .news-item2,
  .news-item3{
    padding: 2.5em 2em;
    order: -1;
  }
}
